<template><section><template>
<blockquote>
<p>Input 输入框</p>
</blockquote>
  <br> 
<h5>通过鼠标或键盘输入字符</h5>
<br>
<br>
<br>
<h3><strong>基础用法</strong></h3>
<br>
  <template>
    <ga-input v-model="input1"></ga-input>
  </template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h3><strong>禁用状态</strong></h3>
<br>
<template>
    <ga-input v-model="input1" disabled></ga-input>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h3><strong>可清空</strong></h3>
<br>
<p>使用<code v-pre="">clearable</code>属性即可得到一个可清空的输入框</p>
<br>
<br>
<template>
  <ga-input v-model="input1" clearable=""></ga-input>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> <span class="hljs-attr">clearable</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h3><strong>带 icon 的输入框</strong></h3>
<br>
<p>带有图标标记输入类型 ( slot方式 )</p>
<br>
<br>
<template>
  <ga-input v-model="input1">
    <ga-icon type="search" slot="pre"></ga-icon>
  </ga-input>
  <ga-input v-model="input1">
    <ga-icon type="calendar" slot="append"></ga-icon>
  </ga-input>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">ga-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"pre"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> &gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">ga-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"calendar"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h3><strong>尺寸</strong></h3>
<br>
<template>
  <ga-input v-model="input1" size="mini">
    <ga-icon type="search" slot="pre"></ga-icon>
  </ga-input>
  <ga-input v-model="input1" size="small" clearable=""></ga-input>
  <ga-input v-model="input1" size="medium">
    <ga-icon type="calendar" slot="append"></ga-icon>
  </ga-input>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"mini"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">ga-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"pre"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">clearable</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"medium"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">ga-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"calendar"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-icon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">ga-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h1>Input Attributes</h1>
  <br>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>input的类型</td>
<td>String</td>
<td>text / number / password</td>
<td>text</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>true / false</td>
<td>false</td>
</tr>
<tr>
<td>clearable</td>
<td>是否可清空</td>
<td>Boolean</td>
<td>true / false</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>输入框尺寸</td>
<td>String</td>
<td>mini / small / medium</td>
<td>small</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入框占位文本</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br>
<h1>Input Slots</h1>
  <br>
<table>
<thead>
<tr>
<th>name</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>pre</td>
<td>输入框前置内容</td>
</tr>
<tr>
<td>append</td>
<td>输入框后置内容</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br> 
<h1>Input Events</h1>
  <br>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>在 Input 值改变时触发</td>
<td>(value: string / number)</td>
</tr>
<tr>
<td>blur</td>
<td>在 Input 失去焦点时触发</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>focus</td>
<td>在 Input 获得焦点时触发</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>preiconclicked</td>
<td>前置icon点击事件</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>appendiconclicked</td>
<td>后置icon点击事件</td>
<td>(event: Event)</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br> 
<h1>Input Methods</h1>
  <br>
<table>
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>使 input 获取焦点</td>
<td>—</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br> 
</template>


</section></template>
<style lang="scss">
    .ga-input{
        margin-right:12px;
    }
</style>
