  export default {
    data () {
      return {
          input1:"123",
          radioData:'radio1',
          radioGroup:"radio-group1",
          radioGroup2:"禁用状态"
      }
    },
    methods:{
      labelChange(val){
        console.log(val)
      }
    }
  }
</script>


